<template>
  <div class="login-contain">
    <el-input v-model="username" placeholder="请输入用户名" class="login-input" prefix-icon="el-icon-user"></el-input>
    <el-input placeholder="请输入密码" v-model="password" show-password class="login-input" prefix-icon="el-icon-lock"></el-input>

    <div class="identify">
      <el-input v-model="inputIdentify" placeholder="请输入验证码" class="login-input" prefix-icon="el-icon-tickets"></el-input>
      <div class="identify-img" @click="randomNum">
        <Sidentify :identifyCode="identify"/>
      </div>
    </div>

    <el-button type="primary" plain class="login-button-left" @click="login">登录</el-button>
    <el-button type="primary" plain class="login-button-right">取消</el-button>

  </div>
</template>

<script>
  import Sidentify from '@/components/Identity'

  export default {
    name: "Login",
    data() {
      return {
        username: '',
        password: '',
        identify: '4047',
        inputIdentify: ''
      }
    },
    components: {
      Sidentify
    },
    methods: {
      //切换验证码
      randomNum() {
        this.identify = (Math.floor(Math.random() * 10000)).toString()
      },
      //实现登录
      login(){
        if(!this.username){
          this.$message({
            showClose: true,
            message: '用户名不能为空',
            type: 'error'
          })
          return
        }
        if(!this.password){
          this.$message({
            showClose: true,
            message: '密码不能为空',
            type: 'error'
          })
          return
        }
        if(this.inputIdentify !== this.identify){
          this.$message({
            showClose: true,
            message: '验证码错误',
            type: 'error'
          })
          return
        }

        this.$axios.post('/blog/api/login',{
          username: this.username,
          password: this.password
        }).then( (res) => {
          if(!res.data[0]){
            this.$message({
              showClose: true,
              message: '登录失败，请检查用户名和密码！',
              type: 'error'
            })
            return
          }else {
            this.$message({
              showClose: true,
              message: '登录成功',
              type: 'success'
            })
            //本地存储登录信息
            localStorage.setItem("userLogin",true)
            localStorage.setItem("cur_user",JSON.stringify(res.data[0]))
            this.$store.dispatch('userLogin',res.data[0])
            this.$router.push('/userinfo/basic')
          }
        })

      }
    }
  }
</script>

<style scoped>
  .login-contain {
    width: 40%;
    margin: 100px auto;
    border: 1px solid #DCDFE6;
    box-shadow: 0 0 8px rgba(0, 0, 0, .4);
    padding: 20px;
    overflow: hidden;
  }

  .login-input {
    margin-bottom: 20px;
  }

  .login-button-left {
    width: 45%;
    float: left;
  }

  .login-button-right {
    width: 45%;
    float: right;
  }

  .identify {
    overflow: hidden;
  }

  .identify .login-input {
    float: left;
    width: 70%;
  }

  .identify-img {
    float: right;
  }

</style>
